<style>
  .choose-title-div:after {
    content: '';
    display: block;
    width: 100%;
    height: 1px;
    background: #4d607d;
    position: absolute;
    bottom: 0;
    left: 0;
    visibility: visible;
  }
  .choose-title-div {
    background-color: #1b2d3f;
    opacity: 0.8;
    border: 1px solid #4d607d;
    color: #d0edff;
    width: 120px;
    text-align: center;
    height: 25px;
    line-height: 25px;
    border-radius: 4px 4px 0 0;
  }
  .org-list {
    width: 100%;
    height: 130px;
    border: 1px solid #4d607d;
    border-radius: 0 4px 4px 4px;
    background-color: #1b2d3f;
    opacity: 0.8;
    padding: 15px;
    transition: color .2s linear,background-color .2s linear,border .2s linear;
  }
  .org-list:hover {
    border: 1px solid #d0edff;
  }
  .org-list:hover:before {
    border-right-color: #d0edff;
  }
  .org-list:before {
    content: '';
    top: -7px;
    left: -6px;
    position: absolute;
    border: 7px solid transparent;
    border-right-color: #4d607d;
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg);
    transition: color .2s linear,background-color .2s linear,border .2s linear;
  }
  .org-out {
    position: absolute;
    right: 11px;
    top: 11px;
    height: 20px;
    width: 19px;
    cursor: pointer;
    background: url('../assets/welcome/close_icon_default.png') no-repeat;
    background-size: 100% 100%;
    transform:rotate(90deg);
    transition: all .2s;
  }
  .org-out:hover {
    background: url('../assets/welcome/close_icon_hover.png') no-repeat;
    background-size: 100% 100%;
    transform:rotate(0deg);
  }
  .icon-required {
    color: red !important;
    margin-right: 2px;
  }

  .welcome-content {
    width: 100%;
    background: url('../assets/welcome/welcome_background.png') no-repeat;
    background-size: 100% 100%;
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    -webkit-flex: 1;
    flex: 1;
    -moz-box-flex: 1;
  }

  .welcome-title {
    width: 100%;
    height: 42px;
    overflow: hidden;
    background: url('../assets/welcome/welcome_title.png') no-repeat;
    background-size: 100% 100%;
  }
  #choose-title-row .org-border {
    padding: 10px;
    cursor: pointer;
    background: url('../assets/welcome/org_border_default.png') no-repeat;
    background-size: 100% 100%;
  }
  #choose-title-row .org-border:hover {
    background: url('../assets/welcome/org_border_active.png') no-repeat;
    background-size: 100% 100%;
  }
  #choose-title-row .org-name {
    position: relative;
  }
</style>
<template>
  <div class="welcome-content">
    <div class="welcome-title">
      <Row style="margin-top: 6px; margin-left: 20px;" v-if="orgList === undefined || orgList === null || orgList.length === 0">
        <img src="../assets/index/lamp.png" style="height: 21px; width: auto; vertical-align: middle;"/>
