        <iCol span="1" v-for="(org, index) in orgList" style="width: 282px;">
          <div @click="toOrg(org)" class="org-border" style="width: 262px; height: 127px; margin-top: 20px; margin-left: 20px; position: relative;">
            <div class="org-name" @click="toOrg(org)">
              <img src="../assets/building/building_default.png" height="20" width="16" style="vertical-align: middle;"/>
              <span style="display: inline-block; vertical-align: middle;">{{org.name}}</span>
            </div>
            <div class="org-out" @click.stop.prevent="orgOut(org)"></div>
          </div>
        </iCol>
      </Row>
    <Modal v-model="addOrgModal" class="default" title="添加新单位" width="700" :mask-closable="false">
      <Form ref="newOrgFrom" :model="newOrg" :label-width="90"  :rules="newOrgValidate">
        <Form-item prop="name" label="名称：">
          <Input type="text" placeholder="输入名称" class="grey" v-model="newOrg.name"/>
        </Form-item>
        <myarea ref="myarea" @select="myareaOut" :address="address"></myarea>
        <form-item prop="addrStreet" :label-width="100" label="详细地址：" style="margin-left: -10px">
          <Input  v-model="newOrg.addrStreet" placeholder="输入详细地址" class="grey" />
        </form-item>
        <Row :gutter="8">
          <iCol span="12">
            <Form-item prop="defaultPid" label="所属平台：">
              <Select v-model="newOrg.defaultPid" placeholder="选择所属平台" size="small" class="grey" style="width: 188px;">
                <Option v-for="platform in platformList" :value=platform.defaultPid>{{platform.platformName}}</Option>
              </Select>
            </Form-item>
          </iCol>
          <iCol span="12">
            <Form-item prop="contactNum" label="单位电话：">
              <Input type="text" class="grey" placeholder="输入单位电话"   v-model="newOrg.contactNum" style="width:188px"/>
            </Form-item>
          </iCol>
        </Row>
        <Row :gutter="8">
          <iCol span="12">
            <Form-item prop="ownerName" label="负责人：">
              <Input type="text" class="grey" placeholder="输入负责人姓名"  v-model="newOrg.ownerName" style="width:188px"/>
            </Form-item>
          </iCol>
          <iCol span="12">
            <Form-item prop="ownerTel" label="电话："  style="margin-left: 3px;">
              <Input type="text" class="grey" placeholder="输入电话" v-model="newOrg.ownerTel" style="width:188px;"/>
            </Form-item>
          </iCol>
        </Row>
        <Row :gutter="8">
          <iCol span="11">
            <Form-item prop="businessType" label="业态：">
              <Select v-model="newOrg.businessType" placeholder="选择业态" size="small" style="width: 188px;" class="grey business-type-select">
                <Option v-for="fireUnitType in fireUnitTypeList" :value=fireUnitType.codeId style="overflow:hidden; text-overflow: ellipsis;">{{fireUnitType.codeValue}}</Option>
              </Select>
            </Form-item>
          </iCol>
          <iCol span="13">
            <Form-item prop="supervLevel" label="消防监管等级：" :labelWidth="118" style="margin-left: 3px;">
              <Select v-model="newOrg.supervLevel" placeholder="选择消防监管等级" size="small" class="grey" style="width: 188px;">
                <Option v-for="supervision in supervisionLevelList" :value=supervision.codeId>{{supervision.codeValue}}</Option>
              </Select>
            </Form-item>
          </iCol>
        </Row>
      </Form>
      <div slot="footer">
        <Button class="grey cancel" @click="addOrgModal = false">取消</Button>
        <Button class="grey" @click="addOrg('newOrgFrom')">保存</Button>
      </div>
    </Modal>
    <!--<Modal v-model="addOrgModal" class="default" title="添加新单位" width="800" :maskClosable="false">-->
