        <!--<iCol span="9"><Input placeholder="单位电话" v-model="newOrg.contactNum" class="grey" /></iCol>-->
      <!--</Row>-->
      <!--<Row style="margin-top: 10px;">-->
        <!--<iCol span="3"  style="line-height:26px; text-align: right;"><span>负责人：</span></iCol>-->
        <!--<iCol span="9"><Input placeholder="负责人/法人姓名" v-model="newOrg.ownerName" class="grey" /></iCol>-->
        <!--<iCol span="3"  style="line-height:26px; text-align: right;"><span>电话：</span></iCol>-->
        <!--<iCol span="9"><Input placeholder="负责人联系电话" v-model="newOrg.ownerTel" class="grey" /></iCol>-->
      <!--</Row>-->
      <!--<Row style="margin-top: 10px;">-->
        <!--<iCol span="3"  style="line-height:26px; text-align: right;"><span>行业：</span></iCol>-->
        <!--<iCol span="9">-->
          <!--<Select :placeholder="'行业'" v-model="newOrg.businessType" class="grey">-->
            <!--<Option value="餐饮">餐饮</Option>-->
            <!--<Option value="商场">商场</Option>-->
            <!--<Option value="工厂">工厂</Option>-->
          <!--</Select>-->
        <!--</iCol>-->
        <!--<iCol span="3"  style="line-height:26px; text-align: right;"><span>消防监管等级：</span></iCol>-->
        <!--<iCol span="9">-->
        <!--<Select :placeholder="'消防监管等级'" v-model="newOrg.supervLevel" class="grey">-->
          <!--<Option value="非重点单位">非重点单位</Option>-->
          <!--<Option value="一级列管">一级列管</Option>-->
          <!--<Option value="二级列管">二级列管</Option>-->
          <!--<Option value="三级列管">三级列管</Option>-->
          <!--<Option value="九小场所">九小场所</Option>-->
